html {
  box-sizing: border-box;
  background: hsl(0, 0%, 87%);
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  overflow-wrap: anywhere;
  background-color: hsl(0, 0%, 87%);
  font-family: 'Montserrat', sans-serif;
}
.container {
    max-width: 1000px;
    margin: 0px auto;
    padding: 20px;
}
.section {
    margin: 10px 0;
    background: white;
}

.section__header {
    border-bottom: 1px solid hsl(0, 0%, 87%);
    padding: 20px;
    margin: 0;
    display: grid;
    grid-template-columns: 1fr auto;
}

@media (max-width: 767px) {
    .section__header {
        grid-template-columns: 1fr;
    }
}

.section__heading {
    font-size: 20px;
}

.section__body {
    padding: 20px;
}

.section__buttons {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 20px;
}

.section__headingButton {
    display: inline;
    background: none;
    border: none;
    color: hsl(180, 100%, 25%);
    transition: 0.2s;
}

@media (max-width: 767px) {
    .section__buttons {
        grid-template-columns: 1fr;
    }
}

.section__headingButton:hover {
    color: hsl(180, 100%, 30%);
}

.section__headingButton:active {
    color: hsl(180, 100%, 35%);
}

.section__headingButton:disabled {
    color: hsl(0, 0%, 87%);
}
.form {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 20px;
}

@media (max-width: 767px) {
    .form {
        grid-template-columns: 1fr;
    }
}

.form__newTask {
    padding: 10px;
    border: 1px solid hsl(0, 0%, 87%);
}

.form__button {
    color: hsl(0, 0%, 100%);
    background: hsl(180, 100%, 25%);
    padding: 10px;
    border: none;
    transition: 0.5s;
}

.form__button:hover {
    background: hsl(180, 100%, 30%);
    -webkit-transform: scale(1.10);
            transform: scale(1.10);
}

.form__button:active {
    background: hsl(180, 100%, 35%);
}
.tasksList {
    list-style: none;
    padding: 0;
    margin: 0;
}

.tasksList__item {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-gap: 10px;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid hsl(0, 0%, 87%);
}

.tasksList__item--hidden {
    display: none;
}

.tasksList__item--done {
    text-decoration: line-through;
}

.tasksList__button {
    border: none;
    color: hsl(0, 0%, 100%);
    width: 30px;
    height: 30px;
    padding: 0px;
    transition: 0.5s;
}

.tasksList__button--toggleDone {
    background: hsl(120, 61%, 34%);
}

.tasksList__button--toggleDone:hover {
    background: hsl(120, 61%, 39%);
}

.tasksList__button--toggleDone:active {
    background: hsl(120, 61%, 44%);
}

.tasksList__button--remove {
    background-color: hsl(348, 83%, 47%);
}

.tasksList__button--remove:hover {
    background-color: hsl(348, 83%, 52%);
}

.tasksList__button--remove:active {
    background-color: hsl(348, 83%, 57%);
}

.tasksList__button--trash {
    height: 30px;
    width: 30px;
}
.section__buttons {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 20px;
}

.section__headingButton {
    display: inline;
    background: none;
    border: none;
    color: hsl(180, 100%, 25%);
    transition: 0.2s;
}

@media (max-width: 767px) {
    .section__buttons {
        grid-template-columns: 1fr;
    }
}

.section__headingButton:hover {
    color: hsl(180, 100%, 30%);
}

.section__headingButton:active {
    color: hsl(180, 100%, 35%);
}

.section__headingButton:disabled {
    color: hsl(0, 0%, 67%);
}
.footer {
    overflow-x: auto;
    background-color: hsl(0, 100%, 99%);
    margin: 30px auto 10px;
    padding: 10px 20px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    align-self: flex-end;
    border-radius: 6px;
    text-align: center;
    font-size: 15px;
    letter-spacing: 0.1em;
}

@media (max-width: 339px) {
    .footer__name {
        font-size: 90%;
    }
}


.footer__link {
    color: hsl(182, 25%, 50%);
    text-decoration: none;
}

.footer__link:hover {
    color: hsl(182, 25%, 35%);
    border-bottom: 1px solid;
    outline: none;
}

.footer__link--git {
    max-width: 30px;
    max-height: 30px;
    border-radius: 50%;
}

.footer__link--git:hover {
    text-decoration: none;
    border-bottom: none;
}
